import { mapState, mapMutations } from 'vuex'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import hTimePicker from "@/components/h-timePicker/h-timePicker.vue"
import ListProject from '@/components/ListProject/ListProject.vue'
import { formatDate } from '@/utils/utils.js'
import JobModel from '@/models/job.js'
import ResumeModel from '@/models/resume.js'
const resumeModel = new ResumeModel()
const jobModel = new JobModel()
export default {
	data() {
		return {
			projectList: [],
			type: 'all',
			// type: 'started',
			pageNum: 1,
			pageSize: 10,
			hasEnd: false,
			jobId: '',
			date: '',
			formId: '' , // 推送相关
		}
	},
	computed:{
		...mapState(['resumeId']),
		
	},
	mounted(){
		this.getJobList()
	},
	components: {
		ListProject,
		uniPopup,
		hTimePicker
	},
	methods: {
		...mapMutations(['switchTab']),
		getJobList(){
			const type = this.type
			const that = this
			let collectionJobStatus = ''
			if(type === 'started'){
				collectionJobStatus = 1
			}else if(type === 'notStart'){
				collectionJobStatus = 0
			}
			if(this.pageNum === 1){
				uni.showLoading({
					mask: true
				})
			}
			jobModel.collectionJobList(this.pageNum, this.pageSize, collectionJobStatus).then(res=>{
				uni.hideLoading()
				uni.stopPullDownRefresh()
				//数据绑定
				const { code, message, data } = res.data
				if(code === '0'){
					if(that.pageNum === 1){
						that.projectList = data.list
					}else {
						that.projectList = [...that.projectList, ...data.list]
					}
					if(that.projectList.length === data.total){
						that.hasEnd = true
					}
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.stopPullDownRefresh()
				uni.showToast({
					icon: 'none',
					title:'获取岗位列表信息失败'
				})
			})
		},
		refresh(){
			this.pageNum = 1
			this.hasEnd = false
			this.getJobList()
		},
		getNext(){
			if(!this.hasEnd){
				this.pageNum += 1
				this.getJobList()
			}
		},
		switchProjectTab(type){
			if(type === this.type){
				return 
			}
			this.type = type
			this.pageNum = 1
			this.hasEnd = false
			this.getJobList()
		},
		show(collectionJobStatus,id){
			// 根据状态判断
		},
		showDetail(id){
			uni.navigateTo({
				url: '../../public/PostDetail/PostDetail?id='+id
			})
			
			// uni.navigateTo({
			// 	url: '../../PostDetail/PostDetail'
			// })
		},
		showDeliveryDetail(id){
			uni.navigateTo({
				url: '../../customer/project/detail?id='+id
			})
		},
		recommend(id){
			this.jobId = id
			if(this.resumeId){
				this.$refs.selectDate.open()
			}else{
				this.switchTab({index:2,jobId:id})
			}
		},
		pushResume(){
			const that = this
			const jobId = this.jobId
			const resumeId = this.resumeId
			const interviewDate = this.date
			const formId = this.formId
			uni.showLoading({
				mask: true
			})
			resumeModel.pushResume(jobId, resumeId, interviewDate, formId).then(res=>{
				uni.hideLoading()
				const { code, message, data } = res.data
				that.formId = ''
				if(code === '0'){
					that.$refs.selectDate.close()
					// 推荐成功
					uni.showModal({
						title: '',
						content: '推荐成功!\r\n您可在“我的项目”栏目查看该人员的面试、入职流程',
						showCancel: false,
						confirmText: '知道了',
						success: res => {},
					})
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				that.formId = ''
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '推送失败'
				})
			})
		},
		cancelPush(){
			this.$refs.selectDate.close()
		},
		bindTimeChange(time){
			this.date = time
		}
	}
}
