import UserItem from '../../../components/UserItem/UserItem.vue'
import UserModel from '@/models/user.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const userModel = new UserModel()
export default {
	data() {
		return {
			userData: {
				// name: '张三'
			},
			user:{},
			dialogActive: 0,
			cashoutList:[]
		}
	},
	components: {
		UserItem,
		uniPopup
	},
	// onLoad(){
	// 	this.getUserInfo()
	// },
	mounted(){
		this.getUserInfo()
	},
	methods: {
		getUserInfo(){
			uni.showLoading()
			userModel.getUserInfo().then(res=>{
				//数据绑定
				uni.hideLoading()
				const { code, message, data } = res.data
				if(code === '0'){
					this.user = data
					this.userData = data
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title:'获取用户信息失败'
				})
			})
		},
		setUser(){
			uni.navigateTo({
				url: '../../customer/user/detail',
			})
		},
		switchItem(index){
			this.dialogActive = index
			if(this.dialogActive == 0){
				this.getCashoutList()
			}else{
				this.getCashoutRecord()
			}
		},
		getCashoutList(){
			this.$refs.recommendRecord.open()
			return
			uni.showLoading({
				title: '加载中...'
			})
			userModel.getCashoutList().then(res=>{
				uni.hideLoading()
				const {code, message, data } = res.data
				if(code === '0'){
					this.cashoutList = data.list
					this.$refs.recommendRecord.open()
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '获取提现信息失败'
				})
			})
		},
		closeCashoutDialog(){
			this.$refs.recommendRecord.close()
		},
		cancelCashOut(){
			this.closeCashoutDialog()
		},
		cashOut(){
			if(this.cashoutList.length>0){
				uni.showLoading({
					title: '请稍等...'
				})
				userModel.cashout().then(res=>{
					uni.hideLoading()
					const {code, message, data } = res.data
					if(code === '0'){
						this.closeCashoutDialog()
						uni.showModal({
							title: '',
							content: '提现申请提交成功!\r\n我们将在三个工作日内与您联系，并汇款到您的账户。',
							showCancel: false,
							confirmText: '好的',
							success: res => {},
						})
					}else{
						uni.showToast({
							icon: 'none',
							title: message
						})
					}
				}).catch(err=>{
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: '提现失败，请稍后再试'
					})
				})	
			}else{
				uni.showToast({
					icon: 'none',
					title: '当前没有可提现'
				})
			}
		},
		// 这是提现记录，需要改地址
		getCashoutRecord(){
			uni.showLoading({
				title: '正在获取...'
			})
			this.cashoutList = []
			userModel.getCashoutList().then(res=>{
				uni.hideLoading()
				const {code, message, data } = res.data
				if(code === '0'){
					this.cashoutList = data.list
					this.$refs.recommendRecord.open()
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '获取提现记录失败'
				})
			})
		},
		// 切换账号
		switchAccount(){
			uni.navigateTo({
				url: '../../public/switchAccount/switchAccount'
			})
		}
	}
}
